<template>
  <div class="carusel" id="projects">
    <div class="container">
      <h2 class="carusel__title">{{$t('myProjects')}}</h2>
      <Carousel :itemsToShow="getItemsToShow()" :wrapAround="true" :transition="2000" :autoplay="true" :autoplayInterval="3000" class="carousel">
        <Slide v-for="slide in slides" :key="slide.id">
          <div class="carousel__item">
            <div class="carousel__content">
              <a :href="slide.link" target="_blank"><img :src="slide.image" alt="" class="carousel__image"></a>
            </div>
          </div>
        </Slide>
      </Carousel>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue'
import { Carousel, Pagination, Slide } from 'vue3-carousel'

import 'vue3-carousel/dist/carousel.css'

export default defineComponent({
  name: 'Autoplay',
  components: {
    Carousel,
    Slide,
    Pagination,
  },
  props: {
    slides: Array,
  },
  methods: {
    getItemsToShow() {
      if (window.innerWidth <= 768) {
        return 3;
      }
      return 4;
    },
  }
})
</script>

